.hs-blog-post {
  .blog-header--post {
    margin-top: 25px;
    border-radius: 30px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
    margin-bottom: 59px;
  }

  .blog-post {
    padding-top: 0;
    max-width: 765px;
    margin: auto;
  }

  .blog-post__body-wrapper {
    max-width: none;
  }

  .body-container--blog-post {
    max-width: 950px;
    margin: auto;
  }

  .blog-post__preheader {
    display: none;
  }

  .blog-post__title {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 48px;
    font-style: normal;
    font-weight: 800;
    line-height: 54px;
    max-width: none;
    margin: 0 auto 16px;
  }

  .blog-post__summary {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    max-width: none;
  }
  .blog-post__meta {
    width: 100%;
    justify-content: center;

    .blog-post__avatar,
    .blog-post__author a {
      display: none;
    }
  }

  .blog-post__timestamp {
    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    opacity: 0.5;
    margin-bottom: 44px;
  }

  .blog-post__divider {
    display: none;
  }

  .blog-post__body {
    p,
    li {
      color: var(--dark-grey-1, #1c1c1c);
      text-align: justify;
      font-size: 14px;
      line-height: 24px;
    }
  }

  h2 {
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 36px;
    font-style: normal;
    font-weight: 800;
    line-height: 42px;
  }

  h3 {
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 24px;
    font-style: normal;
    font-weight: 800;
    line-height: 32px;
    text-decoration-line: underline;
  }
}
