.hs-blog-listing {
  .hs-search-field {
    width: 100%;
    max-width: none;

    form {
      display: flex;
      justify-content: center;
    }
  }

  .hs-search-field input {
    width: 100%;
    max-width: 559px;
    border-radius: 140px;
    border: 2px solid #1c1c1c;
    background: #fcfcfc;
    box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1) inset;
    padding: 22px 37px 22px 30px;
    position: relative;
    font-size: 24px;
    font-style: normal;
    color: #1c1c1c;

    &::placeholder {
      color: #1c1c1c;
      opacity: 0.3;
    }

    &:before {
      background-image: url("https://cdn.jsdelivr.net/gh/Dev-DigiFresh/BlogStylesBuild/img/search.svg");
      content: "";
    }
  }

  .hs-search-field__bar:after {
    display: none;
  }

  .blog-tags {
    margin: 38px auto 0;
    width: 100%;
    text-align: center;
    max-width: 705px;

    li {
      margin: 6px 3px 0 0;
    }
  }

  .blog-tags__link {
    border-radius: 70px;
    border: 2px solid #1c1c1c;
    background: #fcfcfc;
    color: #1c1c1c;
    opacity: 0.3;
    padding: 8px 24px;

    &:hover,
    &.blog-tags__link--current {
      opacity: 1;
      transform: none;
      border: 2px solid #1c1c1c;
      background-color: #fcfcfc;
      color: #1c1c1c;
    }
  }

  .blog-index__post {
    border-radius: 20px;
    border: 2px solid var(--dark-grey-1, #1c1c1c);
    background: var(--white-1, #fcfcfc);
    box-shadow: -6px 6px 0px 0px rgba(28, 28, 28, 0.1);
    padding: 20px;
    height: 486px;
  }

  .blog-index__post-image,
  .blog-more-posts__post-image {
    width: 100%;
    height: 170px;
    padding: 0;
    border-radius: 10px;
    margin-bottom: 16px;
  }

  .blog-index__post-date.digiphy-custom {
    margin-left: 0;
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    opacity: 0.5;
    margin-bottom: 14px;
    display: block;
  }

  .blog-index__post-content {
    padding: 0;

    h3 {
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 24px;
      font-style: normal;
      font-weight: 800;
      line-height: 28px;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 0;
      color: var(--dark-grey-1, #1c1c1c);
      font-size: 12px;
      font-style: normal;
      line-height: 18px;
    }
  }

  .blog-index__post-content h3 a {
    display: -webkit-box;
    -webkit-line-clamp: var(--title-max-lines);
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  .blog-index__post-meta .blog-index__post-author {
    display: none;
  }

  .blog-index__post-meta,
  .blog-more-posts__post-meta {
    margin-top: 0;
  }

  .blog-index__post-preheader.digiphy-custom {
    color: var(--dark-grey-1, #1c1c1c);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration-line: underline;
    margin-bottom: 0;
  }

  .blog-pagination {
    justify-content: center;
    gap: 4px;
  }

  .blog-pagination__link {
    --box-size: 45px;

    color: var(--dark-grey-1, #1c1c1c);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 800;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: var(--box-size);
    width: var(--box-size);

    &--active {
      color: var(--light-grey-1, #f2f2f0);
      font-style: normal;
      border-radius: 10px;
      background: var(--dark-grey-1, #1c1c1c);
    }

    &:hover {
      color: #7b6acc;
    }

    &--active {
      &:hover {
        color: var(--light-grey-1, #f2f2f0);
        background-color: #7b6acc;
      }
    }
  }

  .blog-pagination__ellipsis {
    display: none;
  }
}
